<template>
  <div class="swipe">
    <swiper :options="swiperOption" >
      <swiper-slide v-for="(slide,key) in swiper" :key="key">
        <img :src="slide">
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>

</template>

<script>
  export default {
    name: 'carousel',
    data() {
      return {
        swiperOption: {
          autoplay: {
            el: 3500
          },
          loop:{
            el:true
          },
          pagination: {
            el: '.swiper-pagination'
          }

      }
    }
//    mounted(){
//
//    }
//    mounted() {
//      setInterval(() => {
//        console.log('simulate async data')
//        if (this.swiperSlides.length < 10) {
//          this.swiperSlides.push(this.swiperSlides.length + 1)
//        }
//      }, 3000)
//    }
  },
    props:['swiper'],
  }
</script>
<style scoped lang="less">
  .swipe{
    img{
      display: block;
      width: 100%;
    }
  }
</style>



