<template>
  <div>
    <Mheader>首页</Mheader>
      <!---->
      <div class="content">
        <loading v-if="load"></loading>
        <template v-else>
        <Swiper :swiper="sliders"></Swiper>
        <div class="containt">
          <h3>热门图书</h3>
          <ul class="bookHotList">
            <li v-for="books in hotBooks">
              <img :src="books.bookCover" alt="">
              <h4>{{books.bookName}}</h4>
            </li>
          </ul>
        </div>
        </template>
      </div>
  </div>

</template>
<script>
  import Mheader from "../base/Mheader.vue"
  import Swiper from "../base/Swiper.vue"
  import {getAll} from '../api'
  import loading from '../base/loading.vue'
  export default {
    data(){
      return {
        sliders:[],
        hotBooks:[],
        load:true
      }
    },
    created(){
      this.getAll()
    },
    methods:{
      async getAll(){
        let[sliders,hotBooks] = await getAll();
        this.sliders=sliders;
        this.hotBooks=hotBooks;
        this.load=false;
      },
  },
    computed:{},
    components:{
      Mheader, Swiper,loading
    }
  }
</script>
<style scoped lang="less">
  .containt h3{
    padding:15px 0 0 40px;
  }
  .bookHotList{
    position: relative;
    overflow: hidden;
    margin:20px ;
    padding-bottom: 150px;
    li{
      float: left;
      width: 50%;
      overflow: hidden;
      margin: 10px 0 ;
      border-bottom: 1px dashed #cccccc;
      img{
        width: 100%;
        height: 100%;
      }
      h4{
        height:40px;
        line-height: 40px;
        padding: 0 10px;
        text-align: center;
      }
    }
  }

</style>



